"use client";
import { useState } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const labels = [
  "Jan", "Feb", "Mar", "Apr", "Mei", "Jun",
  "Jul", "Agu", "Sep", "Okt", "Nov", "Des",
];

const data = {
  labels,
  datasets: [
    {
        label: "Tingkat Kemiskinan (%)",
        data: [75, 75, 60, 55, 40, 35, 40, 25, 20, 15, 15, 10],
        backgroundColor: "#2563EB",
        barThickness: 60,
        categoryPercentage: 1.0,
        barPercentage: 1.0,
    },
  ],
};

const options = {
  responsive: true,
  plugins: {
    legend: {
      display: false,
    },
    title: {
      display: true,
      text: "Tingkat Kemiskinan Provinsi Jawa Timur 2024",
      font: {
        size: 18,
      },
      align: "start",
      padding: {
        top: 20,
        bottom: 40,
      },
    },
    tooltip: {
      enabled: true,
    },
  },
  scales: {
    y: {
        beginAtZero: true,
        max: 100,
        ticks: {
        stepSize: 33.3,
        callback: function(value) {
            if (value === 0) return "2021";
            if (value === 33.3) return "2022";
            if (value === 66.6) return "2023";
            if (value === 100) return "2024";
            return "";
        },
        color: "#6B7280",
        },
    },
  },
};

// Komponen chart
const TingkatKemiskinanChart = () => {
  const [selectedYear, setSelectedYear] = useState("2024");
  return (
    <div className="bg-white p-5 rounded-xl flex-1 shadow-md">
        <div className="flex items-center justify-between pt-4 pb-4 border-b-2 border-main-blue/10">
            <div className="flex items-center gap-2">
                <svg className="text-blue-600" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                    <path fill="currentColor" d="M11.875 20q.1 0 .2-.05t.15-.1l8.2-8.2q.3-.3.438-.675t.137-.75q0-.4-.137-.763t-.438-.637l-4.25-4.25q-.275-.3-.638-.437T14.776 4q-.375 0-.75.138t-.675.437l-.275.275l1.85 1.875q.375.35.55.8t.175.95q0 1.05-.712 1.763t-1.763.712q-.5 0-.962-.175t-.813-.525L9.525 8.4L5.15 12.775q-.075.075-.112.163T5 13.125q0 .2.15.363t.35.162q.1 0 .2-.05t.15-.1l2.7-2.7q.275-.275.688-.288t.712.288q.275.275.275.7t-.275.7l-2.675 2.7q-.075.075-.112.163t-.038.187q0 .2.15.35t.35.15q.1 0 .2-.05t.15-.1l2.7-2.675q.275-.275.688-.288t.712.288q.275.275.275.7t-.275.7l-2.675 2.7q-.075.05-.112.15t-.038.2q0 .2.15.35t.35.15q.1 0 .188-.038t.162-.112l2.7-2.675q.275-.275.688-.288t.712.288q.275.275.275.7t-.275.7l-2.7 2.7q-.075.075-.112.163t-.038.187q0 .2.163.35t.362.15m-.025 2q-.925 0-1.637-.612t-.838-1.538q-.85-.125-1.425-.7t-.7-1.425q-.85-.125-1.412-.712T5.15 15.6q-.95-.125-1.55-.825t-.6-1.65q0-.5.188-.962t.537-.813L8.1 6.975Q8.675 6.4 9.513 6.4t1.412.575L12.8 8.85q.05.075.15.113t.2.037q.225 0 .375-.137t.15-.363q0-.1-.038-.2t-.112-.15L9.95 4.575q-.275-.3-.637-.437T8.55 4q-.375 0-.75.138t-.675.437L3.6 8.125q-.35.35-.5.825t-.075.95q.075.425-.175.75t-.675.375t-.75-.188t-.375-.662q-.15-.95.138-1.862T2.175 6.7L5.7 3.175Q6.3 2.6 7.038 2.3T8.55 2t1.513.3t1.312.875l.275.275l.275-.275q.6-.575 1.338-.875t1.512-.3t1.513.3t1.312.875L21.825 7.4q.575.575.875 1.325t.3 1.525t-.3 1.513t-.875 1.312l-8.2 8.175q-.35.35-.812.55t-.963.2M9.4 8" />
                </svg>
                <h2 className="text-2xl font-semibold text-gray-500">Perkembangan Tingkat Kemiskinan Provinsi Jawa Timur (%)</h2>
            </div>
            <select
                value={selectedYear}
                onChange={(e) => setSelectedYear(e.target.value)}
                className="border-2 border-blue-600 text-blue-600 rounded-md py-2 px-4 text-sm font-bold cursor-pointer"
            >
                <option className="text-blue-600" value="2024">2024</option>
                <option className="text-blue-600" value="2023">2023</option>
                <option className="text-blue-600" value="2022">2022</option>
                <option className="text-blue-600" value="2021">2021</option>
            </select>
        </div>
      <Bar data={data} options={options} />
    </div>
  );
};

export default TingkatKemiskinanChart;
